.banner_box{
    width: 1100px;
    height: 450px;
    margin: auto;
    box-sizing: border-box;    
    margin-top: 15px;
    position: relative;
    overflow: hidden;
}
.banner_ul{
    width: 3300px;
    height: 450px;
    margin: auto;
    box-sizing: border-box;
    position: absolute;
}    
.bli1{
    width: 1100px;
    height: 450px;
    box-sizing: border-box;
    background-image: url(../public/banners/banner0.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    float: left;
}
.bli2{
    width: 1100px;
    height: 450px;
    box-sizing: border-box;
    background-image: url(../public/banners/banner1.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    float: left;
}
.bli3{
    width: 1100px;
    height: 450px;
    box-sizing: border-box;
    background-image: url(../public/banners/banner2.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    float: left;
}
.banner_box > ul:nth-of-type(2){
    width: 150px;
    height: 40px;
    color: white;
    position: absolute;
    text-align: center;
    line-height: 40px;
    right: 20px;
    bottom: 30px;
}
.banner_box > ul:nth-of-type(2) > li{
    width: 30px;
    height: 30px;
    line-height: 30px;
    background-color: gray;
    float: left;
    margin-left: 10px;
    cursor: pointer;
}
.banner_box > ul:nth-of-type(2) > li:nth-of-type(1){
    background-color: #cccccc;
}