.top_box{
    width: 100%;
    height: auto;
    font-size: 14px;
    color: #383838;
    margin: auto;
}
.top_adbox{
    width: 1100px;
    height: auto;
    margin: auto;
}
.top_ulbox2{
    width: 100%;
    height: 110px;
    background-color: white;
    text-align: center;
}
.top_ulbox1{
    width: 100%;
    height: 40px;
    background-color: #dfdede;
    text-align: center;
}
.top_ulcss{
    width: 1100px;
    height: 40px;
    display: inline-block;
    position: relative;
    right: 0;
    text-align: right;
    background-color: #dfdede;
}

.top_ulcss > li {
    display: inline-block;
    height: inherit;
    line-height: 40px;
    padding: 0 20px 0  20px;
    box-sizing: border-box;
    cursor: pointer;
}
.top_ulcss > li:hover{
    color: blueviolet;
}
.top_ad{
    width: 1100px;
    height: 70px;
    background-image: url(../public/ad/top_ad.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: relative;
}
.top_ulcss > li:nth-of-type(4){
    color: #E82115;
}
.top_ulcss > div{
    position: absolute;
    left: 10px;
    top: 10px;
}
.top_ad > span{
    display: block;
    width: 25px;
    height: 25px;
    background-color: black;
    color: white;
    text-align: center;
    line-height: 25px;
    position: absolute;
    right: 25px;
    top: 22.5px;
    cursor: pointer;
}
.top_ulcss2{
    margin: auto;
    width: 1100px;
    height: 110px;
    display: inline-block;
}
.top_ulcss2 > li{
    text-align: left;
    height: inherit;
    line-height: 110px;
    float: left;
    box-sizing: border-box;
    background-color: white;
}
.top_ulcss2 > li:nth-of-type(1){
    width: 330px;
}
.top_ulcss2 > li:nth-of-type(2){
    width: 440px;
}
.top_ulcss2 > li:nth-of-type(3){
    width: 330px;
    text-align: right;
}
.top_ulcss2 > li:nth-of-type(1) > img{
    margin-top: 30px;
    cursor: pointer;
}
.top_ulcss2 > li:nth-of-type(3) > img{
    margin-top: 25px;
    cursor: pointer;
}
.top_ulcss2 > li:nth-of-type(2) > div{
    margin: auto;
    width: 370px;
    height: 40px;
    line-height: 40px;
    margin-top: 30px;
    border-bottom: 4px solid #ed4747;
}
.top_ulcss2 > li:nth-of-type(2) > div > input{
    width: 340px;
    height: 40px;
    outline: none;
    box-sizing: border-box;
    float: left;
    border: 0;
    text-indent: 10px;
}
.toptext{
    display: block;
    width: 30px;
    height: 30px;
    box-sizing: border-box;
    background-image: url(../public/topimage/top_btn_search.gif);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    float: left;
    margin-top: 5px;
    cursor: pointer;
}