.menu_box{
    width: 1100px;
    height: auto;
    background-color: white;
    margin: auto;
}
.menu_ul{
    width: 1100px;
    height: 50px;
    border: 1px solid black;
}
.menu_ul > li{
    font-size: 15px;
    font-weight: 400;
    float: left;
    width: 126px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    box-sizing: border-box;
    cursor: pointer;
}
.menu_ul > li:nth-of-type(1){
    width: 200px;
    color: #e82115;
    font-weight: 500;
    border-right: 1px solid black;
}
.menu_ul > li:nth-of-type(7){
    width: 135px;
    color: #222222;
    border-left: 1px solid black;
    position: relative;
}
.menu_ul > li:nth-of-type(8){
    width: 135px;
    color: #222222;
    position: relative;
}
.menu_img1{
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    background-image: url(../public/topimage/top_ico_notice.gif);
    background-repeat: no-repeat;
    background-size: contain;
    top: 18px;
    left: 20px;
}
.menu_img2{
    display: block;
    width: 15px;
    height: 15px;
    position: absolute;
    background-image: url(../public/topimage/top_ico_qa.gif);
    background-repeat: no-repeat;
    background-size: contain;
    top: 18px;
    left: 20px;
}
.menu_ul > li:hover{
    color: #e82115;
}